import './style/style.scss';

export class Blockqa {
    constructor() {
        this.init();
    }

    init() {
        this.accordions = document.querySelectorAll('.accordion-item')
        if (this.accordions.length) {
            [].forEach.call(this.accordions, accordion => {
                accordion.addEventListener('click', this.clickHandler.bind(this))
            })
        }
    }

    clickHandler(evt = new Event('click'))
    {
        const el = evt.currentTarget || evt.target
        if (el) {
            const modalElem = el.querySelector('.answer')
            const isOpen = modalElem.classList.contains('active')
            this.closeAnswer ()

            if (!isOpen) {
                const modalElem = el.querySelector('.answer')
                const openBut = el.querySelector('.js-open-qa')
                const closeBut = el.querySelector('.js-close-qa')

                modalElem.classList.add('active')
                openBut.classList.remove('visible');
                closeBut.classList.add('visible');
            }
        }
    }

    closeAnswer () {
        [].forEach.call(this.accordions, point => {
            const modalElem = point.querySelector('.answer')
            const openBut = point.querySelector('.js-open-qa')
            const closeBut = point.querySelector('.js-close-qa')

            modalElem.classList.remove('active')
            openBut.classList.add('visible')
            closeBut.classList.remove('visible')
        })
    }
}